@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(//db.onlinewebfonts.com/c/f4d1593471d222ddebd973210265762a?family=Pokemon);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card{display:flex;flex-direction:column;height:400px;width:300px;position:relative}.card .front{z-index:2;-webkit-transform:rotateY(0deg);transform:rotateY(0deg);text-align:center}.card .front .img-pokemon{align-items:center;display:flex;flex:3 1;justify-content:center;overflow:hidden}.card .front .img-pokemon img{width:100%}.card .front .name-pokemon{font-size:32px;font-weight:700;font-family:'Bangers', cursive;text-transform:capitalize;letter-spacing:5px}.card .back{-webkit-transform:rotateY(-180deg);transform:rotateY(-180deg);display:flex;flex-direction:column;background:#D3CCE3;background:linear-gradient(to right, #E9E4F0, #D3CCE3)}.card .back h2{text-align:center;text-transform:uppercase;padding:10px 0}.card .back table{width:100%;padding:0 10px;font-size:18px}.card .back table tr td{padding:5px 0;border-bottom:1px solid rgba(0,0,0,0.6)}.card .back table tr:nth-last-of-type(1){border-bottom:none}.card .front,.card .back{height:100%;width:100%;display:block;box-shadow:0 4px 8px 0 rgba(0,0,0,0.6);position:absolute;-webkit-backface-visibility:hidden;backface-visibility:hidden;transition:-webkit-transform ease .8s;transition:transform ease .8s;transition:transform ease .8s, -webkit-transform ease .8s}.card__side .front{-webkit-transform:rotateY(-180deg);transform:rotateY(-180deg)}.card__side .back{-webkit-transform:rotateY(0deg);transform:rotateY(0deg)}@media only screen and (min-width: 500px){.pokemon_list{padding:30px;justify-content:center;justify-items:center;display:grid;grid-gap:30px;grid-template-columns:repeat(auto-fit, 300px)}}*{margin:0;padding:0}*,*::after,*::before{box-sizing:inherit}html{box-sizing:border-box;font-size:62.5%}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-weight:400;min-height:100vh}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.btn{border:none;padding:15px 32px;text-align:center;text-decoration:none;display:block;font-size:16px;cursor:pointer}.btn--load_more{width:100%;background-color:#D3CCE3;font-size:1.6rem;font-weight:bold}.App{max-width:120rem;min-height:60rem;margin:8rem auto;box-shadow:0 2rem 6rem rgba(0,0,0,0.3);display:flex;flex-direction:column;justify-content:space-between}@media only screen and (max-width: 75rem){.App{margin:0;max-width:100%;width:100%}}.App .header{font-size:1.4rem;display:flex;justify-content:space-between;align-items:center}@media only screen and (max-width: 31.25rem){.App .header{flex-wrap:wrap}}.main-title{width:50%;padding:2rem}@media only screen and (max-width: 31.25rem){.main-title{width:100%;text-align:center;margin:0}}.filter-view{width:50%;display:flex;justify-content:space-between}.filter-view span{font-size:1.6rem;font-weight:bold}.filter-view .select-type{outline:none;width:80%;display:block;color:#444;padding:.6em 1.4em .5em .8em;margin:0 2rem;box-shadow:0 1px 0 1px rgba(0,0,0,0.04);border-radius:.5em;-moz-appearance:none;-webkit-appearance:none;appearance:none;background-color:transparent}@media only screen and (max-width: 75rem){.filter-view{flex-direction:column}.filter-view span{display:block}.filter-view .select-type{width:90%;margin:0}}@media only screen and (max-width: 31.25rem){.filter-view{width:100%}.filter-view .select-type{width:100%}}

